import request from '@/utils/request'

export const getUserInfo = (data: any) =>
  request({
    url: '/system/users/profile',
    method: 'post',
    data
  })

export const login = (data: any) =>
  request({
    url: '/system/users/login',
    method: 'post',
    data
  })

export const logout = () =>
  request({
    url: '/system/users/logout',
    method: 'post'
  })
